import React from 'react';
import ReactDOM from 'react-dom';
import {createStore, applyMiddleware} from 'redux'
import Logger from 'redux-logger'
import {Provider} from 'react-redux'
import {Route, Switch} from 'react-router'
import {createHashHistory} from 'history';
import {ConnectedRouter, routerMiddleware, connectRouter} from 'connected-react-router';
import thunk from 'redux-thunk'
import {rootReducer} from './reducers'

import MainView from './containers/Main/MainView';
import Header from './containers/Header/Head';
import Footer from './containers/Footer'
import Advantages from './containers/Advantages/Advantages';
import Offers from './containers/Offers/index'
import Service from './containers/Service/index'

import UnkownPage from './containers/404/index'

import registerServiceWorker from './registerServiceWorker';

const history = createHashHistory({hashType: "noslash"})

const store = createStore(connectRouter(history)(rootReducer), window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__(), applyMiddleware(thunk, routerMiddleware(history), Logger))

ReactDOM.render(
    <Provider store={store}>
    <ConnectedRouter history={history}>
        <div >
            <Header/>
            <Switch>
                <Route exact path='/' component={MainView}/>
                <Route path='/main' component={MainView}/>
                <Route path='/advantages' component={Advantages}/>
                <Route path='/offers' component={Offers}/>
                    <Route path='/test' component={UnkownPage}/>
                <Route
                    path='/service/:type'
                    component={(props) => <Service type={props.match.params.type}/>}/>

                <Route component={UnkownPage}/>
            </Switch>
            <Footer/>
        </div>
    </ConnectedRouter>
</Provider>, document.getElementById('root'));
registerServiceWorker();



