import React, {Component} from 'react'
import InlineSvg from 'svg-inline-react'
import PropTypes from 'prop-types'

export default class Zvezda extends Component {
    render() {
        const {opacity} = this.props
        const ZvezdaSource = `<svg xmlns="http://www.w3.org/2000/svg" viewBox="26.308780670166016 29.474998474121094 147.35842895507812 141.08694458007812" role="img" preserveAspectRatio="xMidYMid meet" style="stroke-width: 0px;"><g><path d="M170.1 94.5c7-6.8 3.1-18.7-6.5-20.1L133.1 70c-3.8-.6-7.1-3-8.9-6.4L110.6 36c-4.3-8.7-16.8-8.7-21.1 0L75.8 63.6c-1.7 3.5-5 5.9-8.9 6.4l-30.5 4.4c-9.7 1.4-13.5 13.3-6.5 20.1L52 116c2.8 2.7 4 6.6 3.4 10.4l-5.2 30.4c-1.6 9.6 8.4 16.9 17.1 12.4l27.3-14.3c3.4-1.8 7.5-1.8 11 0l27.3 14.3c8.6 4.5 18.7-2.8 17.1-12.4l-5.2-30.4c-.7-3.8.6-7.7 3.4-10.4l21.9-21.5z"></path></g></svg>`
        return (
            <div
                
                style={{
                fill: '#3D9BE9',
                stroke: '#ED1566',
                width:'25px',
                
                fillOpacity: opacity,
                strokeOpacity: 0,
                strokeWidth: 0
            }}>
                <InlineSvg src={ZvezdaSource}/>
            </div>
        )
    }
}


Zvezda.propTypes = { 
    opacity:PropTypes.string.isRequired,
}


