import React, {Component} from 'react'
import {connect} from 'react-redux'
import {platform} from '../../utils/platform'
import Card from '../../components/Service/BuildCard';
import {getService} from '../../reducers/init/reducer'
import {CardColumns} from 'reactstrap'
import './service.css'
export class Service extends Component {
    render() {
        const {isUnkown} = this.props
        const isMobile = platform()
        if (!isUnkown) {
            const {lozung, lead, text, items} = this.props.Service
            return (
                <div className='container'>

                    <div className="jumbotron ">
                        <div className='container'>
                            <h1
                                className={isMobile
                                ? 'display-5'
                                : 'display-4'}>{lozung}</h1>
                            <p className="lead">{lead}</p>
                            <p>{text}</p>
                            <hr className="my-4"/>
                            <div className='container'>
                                <CardColumns >{items.map((val, i) => <Card key={i} cardValue={val}/>)}</CardColumns>
                            </div>
                        </div>
                    </div>
                </div>
            )
        } else {
            return (
                <div className='container mt-5'>
                    <h1>,  ,  c    </h1>
                    <h2>404 - not found</h2>
                    <br/>
                    <h3>   ,     ^_^</h3>
                    <h5 className='float-right'>  ,  ; )</h5>
                    <br/>
                </div>
            )
        }
    }
}

const mapStateToProps = (state, ownProps) => {
    const a = getService(ownProps.type, state)
    if (a) {
        return {isUnkown: false, Service: a}
    } else {
        return {isUnkown: true}
    }
}

export default connect(mapStateToProps)(Service)


